export default {
	data() {
		return {
			details: '',
			loadEnd: false
		}
	},
	onLoad(options) {
		this.loadData(options.id)
	},
	methods: {
		loadData(id) {
			const that = this;
			uni.request({
				url: "https://api.cat-shop.penkuoer.com/api/v2/proxy",
				method: 'POST',
				timeout: 8000,
				data: {
					url: 'http://v1.alapi.cn/api/new/detail?docid=' + id
				},
				success(res) {
					that.details = res.data.data[id];
					that.loadEnd = true;
				},
				fail(err) {
					console.log(err)
				}
			})
			// uni.request({
			// 	url: 'http://v1.alapi.cn/api/new/detail?docid=' + id,
			// 	timeout: 8000,
			// 	success(res) {
			// 		that.details = res.data.data[id];
			// 		that.loadEnd = true;
			// 	},
			// 	fail(err) {
			// 		console.log(err)
			// 	}
			// })
		}
	}
}
